"use client";

import { Card, CardTitle, CardContent, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import { Input } from "@/components/ui/input";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { useForm, SubmitHandler } from "react-hook-form";
import { id, i, init, type InstaQLEntity } from "@instantdb/react";

// ID for app: finance-app
const APP_ID = "e1067022-0a5c-472c-a97b-b3caf26ac02d";


// finance schema
const financeSchema = i.schema({
  entities: {
    finances: i.entity({
      description: i.string(),
      transactionAmount: i.number(),
      transactionCost: i.number(),
      paidOn: i.number(),
    })
  }
})

type Finance = InstaQLEntity<typeof financeSchema, "finances">;

const financeDb = init({ appId: APP_ID, financeSchema });

export function App() {
  // read data
  const { isLoading, error, data } = financeDb.useQuery({ finances: {} })
  if (isLoading) {
     return;
  }
  if (error) {
    return <div className=" text-red-500">Error: { error.message }</div>
  }
  
  const { finances } = data;

  return (
    <div className="font-mono mt-10 flex justify-center items-center flex-col space-y-4">

      <UiCards />

      <FormComponent />

    </div>
  )
}

// Write transaction
function addExpense( description: string, transactionCost: number, transactionAmount: number) {
  financeDb.transact(
    financeDb.tx.finances[id()].update({
      description,
      transactionAmount,
      transactionCost,
      paidOn: Date.now()
    })
  )
}

// delete
function deleteTransaction( finance: Finance) {
  financeDb.transact(financeDb.tx.finances[finance.id].delete());
}


export default App;


function UiCards () {

  const transactionData = {
    cost: 2.99,
    amount: 50.0,
    total: 52.99,
  }
  return (
    <div className="border border-gray-300 max-w-sm w-full flex flex-col items-center p-4">
      <div className="w-full space-y-3">
        <Card className="w-full bg-green-50">
          <CardHeader className="py-2 px-4">
            <CardTitle className="text-sm font-medium text-green-700">Transaction Cost</CardTitle>
          </CardHeader>
          <CardContent className="py-2 px-4">
            <p className="text-lg font-semibold text-green-600">${transactionData.cost.toFixed(2)}</p>
          </CardContent>
        </Card>

        <Card className="w-full bg-primary text-primary-foreground">
          <CardHeader className="py-2 px-4">
            <CardTitle className="text-sm font-medium">Amount</CardTitle>
          </CardHeader>
          <CardContent className="py-2 px-4">
            <p className="text-lg font-semibold">${transactionData.amount.toFixed(2)}</p>
          </CardContent>
        </Card>

        <Card className="w-full bg-rose-50">
          <CardHeader className="py-2 px-4">
            <CardTitle className="text-sm font-medium text-rose-700">Total</CardTitle>
          </CardHeader>
          <CardContent className="py-2 px-4">
            <p className="text-lg font-semibold text-rose-600">${transactionData.total.toFixed(2)}</p>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}

function FormComponent() {
  return (
    <Dialog>
      <DialogTrigger asChild>
        <Button variant="outline">Add Expense</Button>
      </DialogTrigger>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>Expense</DialogTitle>
          <DialogDescription>
            Add amount and cost. Click save when you're done.
          </DialogDescription>
        </DialogHeader>
        <div className="grid gap-4 py-4">
          <div className="grid grid-cols-4 items-center gap-4">
            <Label htmlFor="name" className="text-right">
              Amount
            </Label>
            <Input
              id="name"
              defaultValue="1050"
              className="col-span-3"
            />
          </div>
          <div className="grid grid-cols-4 items-center gap-4">
            <Label htmlFor="username" className="text-right">
              Cost
            </Label>
            <Input
              id="username"
              defaultValue="12"
              className="col-span-3"
            />
          </div>
        </div>
        <DialogFooter>
          <Button type="submit">Save changes</Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  )
}